import React from 'react'
import Layout from "../../components/layout"
import { Link } from 'gatsby'
import SEO from "../../components/seo"
import styles from './index.module.css';

const CalculatorItem = ({children, description, ...props}) => {

  return (
      <div className={styles.calculator_item}>
        <Link to={props.to} className={styles.fw}>
          <div>
            <div><h3>{children}</h3><hr /></div>
            {description && <div ><p>{description}</p></div>}
          </div>
        </Link>
      </div>
  );
};

const Calculator = ({location}) => {
   return (
      <Layout location={location} title={"Calculators"} withFooter={false}>
        <SEO
          title={"Calculators."}
          description={"List of all calculators I have created over time."}
        />
        <div className={styles.calculator_grid}>
          <CalculatorItem
            to={"/calculators/money_prediction"}
            description="Do you know how much money will you have in the future? Let's look on that and compute that."
          >
            Future money
          </CalculatorItem>
          <CalculatorItem
            to={"/calculators/count_words"}
            description="Easy words counter. Copy paste your text, you will see how many words are there."
          >
            Words Counter
          </CalculatorItem>
          <CalculatorItem
            to={"/calculators/inflation"}
            description="This calculator computes how the inflation impacts the value of your money."
          >
            Money inflation
          </CalculatorItem>
        </div>
      </Layout>
   );
};

export default Calculator
